import backTop from '@/components/other/back-top/back-top.vue'
import gameItem2 from '@/components/k-ui/k-game-list/k-game-item.vue'
import uniNoticeBar from '@/components/uni-ui/uni-notice-bar/uni-notice-bar.vue'
import uniDrawer from '@/components/uni-ui/uni-drawer/uni-drawer.vue'
import uniLoadMore from '@/components/uni-ui/uni-load-more/uni-load-more.vue'
import kMoreBtn from "@/components/k-ui/k-more-btn/k-more-btn.vue"
export default {
	components: {
		gameItem2,
		uniNoticeBar,
		uniDrawer,
		backTop,
		uniLoadMore,
		kMoreBtn
	},
	data() {
		return {
			// game的数据
			games: [],
			currentPage: 1,
			categoryId: -1,
			categoryIndex: 0,
			// 左侧边栏展开
			drawerVisiable: false,
			categories: [],
			// keyAction: 0,
			//回到顶部
			backTop: {
				src: 'http://www.iamk.top/static/back-top/top.png',
				scrollTop: 0
			},
			titleNViewBackground: 'rgb(205, 215, 218)',
			swiperCurrent: 0,
			swiperLength: 4,
			carouselList: [{
				id: 0,
				src: "http://weteam.sys.iamk.top/weteam2.png"
			}],
			bgColor: ["rgb(205, 215, 218)", "#e07472", "rgb(203, 87, 60)", "#a8a8a8"],
			//回到顶部,
			animateName: 'animated bounceInRight',
			loadingType: 'more',
			// 更多操作 最前面的显示在最下方
			moreOpList:[
				{
					"title":"版本日志",
					"icon": "formfill",
					"color": "#ff9900",
					"url": "/pages/user/log"
				},
				{
					"title":"问题反馈",
					"icon": "questionfill",
					"color": "#dd6161",
					"url": "/pages/user/user"
				},
				{
					"title":"发现新活动",
					"icon": "activityfill",
					"color": "#19be6b",
					"url": "/pages/user/want"
				},
				{
					"title": "发现新竞赛",
					"icon": "upstagefill",
					"color": "#55aaff",
					"url": "/pages/user/want"
				}]
		}
	},
	onLoad() {
		uni.showLoading({
			title: '正在加载'
		})
		this.loadSlideshow()
		this.getCategories()
		this.loadData()
	},
	onHide() {
		this.drawerVisiable = false
		// this.currentPage = 1
		// this.categoryId = -1
		// this.games = []
		// this.carouselList = []
	},
	onShow() {
		// this.loadData()
		// this.loadSlideshow()
		// const scene = docodeUrlComponent(query)
	},
	methods: {
		clickMoreBtn(e){
			if(e==-1) {
				return
			}
			// console.log(e)
			if(e==1) {
				uni.switchTab({
					url: this.moreOpList[e].url
				})
				return
			}
			uni.navigateTo({
				url: this.moreOpList[e].url
			})
		},
		onKeyChange(e) {
			console.log(e)
			// uni.showLoading({
			// 	title: '正在加载'
			// })
			if(e.detail <=1) {
				this.categoryId = e.detail-1;
			} else {
				console.log(e.detail)
				console.log(this.categories[e.detail - 2])
				console.log(this.categories[e.detail - 2].category)
				this.categoryId = this.categories[e.detail - 2].id
			}
			this.categoryIndex = e.detail
			console.log(this.categoryId)
			this.games = []
			this.currentPage = 1;
			this.loadData()
			this.drawerVisiable = false
		},
		async loadData(status = 'add') {
			if (status == "refresh") {
				this.currentPage = 1
				this.categoryId = -1
				this.games = []
			} else if (status = 'add') {
				if (this.loadingType === 'nomore') return;
				this.loadingType = 'loading'
			} else {
				this.loadingType = 'more'
			}
			// this.loadingType = 'loading'
			const res = await this.$api.request({
				url: '/game/gameList',
				method: 'GET',
				data: {
					id: this.categoryId,
					currentPage: this.currentPage,
					pageSize: this.$common.pageSize
				}
			})
			console.log(res)
			this.games = [...this.games, ...res.data]
			uni.hideLoading()
			if (res.data.length == 0 || res.data.length < this.$common.pageSize) {
				this.loadingType = 'noMore'
			}
		},
		async loadSlideshow() {
			if (this.carouselList.length >= 4) return;
			const _this = this
			const res = await this.$api.request({
				url: '/game/getSlideshow'
			})
			console.log(res)
			if (res.data.length != 0) {
				res.data.forEach(function(r) {
					const a = {
						'id': r.id,
						'src': r.poster_url
					}
					_this.carouselList.push(a)
				})
			}
		},
		async getCategories() {
			const res = await this.$api.request({
				url: '/category',
				method: 'GET',
			})
			this.categories = res.data
			// console.log(this.categories)
		},
		animateCSS() {
			// let _animateName = this.animateName
			this.animateName = ''
			// this.animateName = 'animated bounceInRight'
			// 回到初始状态
			setTimeout(function() {
				// this.animateName = ''
				this.animateName = 'animated bounceInRight'
			}.bind(this), 0);
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.bgColor[index];
		},
		//详情页
		navToDetailPage(item) {
			this.$api.msg(`点击详细`);
		},
		// 跳转到比赛详情
		goGameDetail(id) {
			uni.navigateTo({
				url: '/pages/game/game-detail?id=' + id
			})
		},
		swiperClick(id) {
			if (id == 0) {
				uni.navigateTo({
					url: '/pages/notice/manual'
				})
				return
			}
			uni.navigateTo({
				url: '/pages/game/game-detail?id=' + id
			})
		},
		navTo(url) {
			uni.navigateTo({
				url
			})
		},
	},
	onReachBottom() {
		if (this.games.length < this.currentPage * this.$common.pageSize) {
			this.loadingType = 'noMore'
			return;
		}
		uni.showLoading({
			title: '正在加载'
		})
		this.currentPage++;
		this.loadData()
	},
	onShareAppMessage() {
		return {
			desc: 'weTeam呀',
			title: 'npu的小瓜子们，快来查看近期竞赛资讯吧~',
			imageUrl: 'http://weteam.sys.iamk.top/weteamLogo2.png',
			path: '/pages/index/index',
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		uni.showLoading({
			title: '正在加载'
		})
		this.currentPage = 1
		this.categoryId = -1
		this.categoryIndex = 0
		this.games = []
		this.loadData()
		setTimeout(() => {
			uni.stopPullDownRefresh();
			uni.showToast({
				title:'刷新成功'
			})
		}, 1000)

	},

}
